.strategies {
    padding: 30px 0;
    @include media-tablet {
        text-align: center;
    }
    @include media-mobile {
        text-align: center;
    }
}

.strategies__title {
    margin: 0 0 14px;
}

.strategies__description {
    margin: 0 0 24px;
}

.strategies__tags {
    margin-bottom: 31px;
    @include media-tablet {
        justify-content: center;
    }

}

.strategy-wrapper {
    > * {
        padding-bottom: 30px;
        @include media-tablet {
            width: 48%;
            flex-basis: 48%;
        }
        @include media-mobile {
            flex-basis: 100%;
        }
    }
}

.strategy {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    padding-left: 5px;
    @include media-tablet {
        overflow: hidden;
        padding-top: 200px;
    }
    &_hidden {
        display: none;
    }
    cursor: pointer;
    &:hover {
        transform: scale(1.04);
        @extend %transition;
    }
    &:not(:hover) {
        transform: scale(1);
        transition: all .2s ease-out;
    }
    
}

.strategy__content {
    padding: 24px 33px 26px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
   
}

.strategy__name {
    margin: 0 0 29px;
    text-align: left;
    // flex-grow: 1;
}

.article-modal__content {
    .strategy__image {
        width: 100%;
        @include media-tablet {
            position: static;
        }
    }

    .strategy__date {
        @include font (1.3rem, 1.4rem, normal, normal);
        margin: 0 0 5px;
    }

    .strategy__name {
        margin-bottom: 10px;
    }

    .strategy__text {
        margin: 0 0 12px;
    }
}