%hamburger-line {
    display: inline-block;
    position: absolute;
    content: "";
    background: $color-hamburger;
    border-radius: 10px;
    height: 3px;
    width: 100%;
}

.hamburger {
    display: none;
    position: relative;
    width: 31px;
    height: 25px;
    margin-right: 37px;

    @include media-tablet {
        display: inline-block;

    }
    &::before {
        @extend %hamburger-line;
        top: 0;
        left: 0;
    }
    &::after {
        @extend %hamburger-line;
        bottom: 0;
        left: 0;
    }
}

.hamburger__line {
   @extend %hamburger-line;
   top: calc(50% - 1.5px);
   left: 0;
}